<template>
<div id="nave">
<div class="phone-viewport">
<md-toolbar class="flex-nave">
  <md-button class="md-icon-button" @click.native="toggleLeftSidenav">
    <md-icon>menu</md-icon>
  </md-button>

  <!-- <md-button @click.native="$router.push('/medir')">MEDIR</md-button> -->
  <div>Datos actualizados el 03/04/2017 </div>
  <img class="unsamlogo" src="medir/static/LogoSecInvHorizontalFondoTranspBlanco.gif" width="130">
  <!-- <img class="unsamlogo" src="../../static/LogoSecInvHorizontalFondoTranspBlanco.gif" width="130"> -->
</md-toolbar>

<md-sidenav class="md-left" ref="leftSidenav" @open="open('Left')" @close="close('Left')">
  <md-toolbar class="md-large">
    <div class="md-toolbar-container">
      <img class="unsamlogo" src="medir/static/LogoSecInvHorizontalFondoTranspBlanco.gif" width="130">
      <!-- <img class="unsamlogo" src="../../static/LogoSecInvHorizontalFondoTranspBlanco.gif" width="130"> -->
    </div>
  </md-toolbar>
<md-list>
<md-list-item><router-link to="/medir" @click.native="toggleLeftSidenav">MEDIR</router-link></md-list-item>
</md-list>
</md-sidenav>
</div>
</div>
</template>

<script>
export default {
  name: 'nave',
  methods: {
    toggleLeftSidenav () {
      this.$refs.leftSidenav.toggle()
    }
  }
}
</script>



