              'rgba(0, 194, 224,0.5)',
              'rgba(0, 194, 224,0.3)'
            ],
            borderColor: [
              'rgba(0, 194, 224,1)',
              'rgba(0, 194, 224,1)',
              'rgba(0, 194, 224,1)',
              'rgba(0, 194, 224,1)'
            ],
            borderWidth: 1
          },
          {
            label: 'EPYG',
            data: [this.personalEnMemoriaEPYG.length, this.personalRelevanteEPYG.length, this.personalProductivoEPYG.length, this.personalNuloEPYG.length],
            backgroundColor: [
              'rgba(81, 232, 152,0.9)',
              'rgba(81, 232, 152,0.7)',
              'rgba(81, 232, 152,0.5)',
              'rgba(81, 232, 152,0.3)'
            ],
            borderColor: [
              'rgba(81, 232, 152,1)',
              'rgba(81, 232, 152,1)',
              'rgba(81, 232, 152,1)',
              'rgba(81, 232, 152,1)'
            ],
            borderWidth: 1
          },
          {
            label: 'IK',
            data: [this.personalEnMemoriaIK.length, this.personalRelevanteIK.length, this.personalProductivoIK.length, this.personalNuloIK.length],
            backgroundColor: [
              'rgba(196, 201, 204,0.9)',
              'rgba(196, 201, 204,0.7)',
              'rgba(196, 201, 204,0.5)',
              'rgba(196, 201, 204,0.3)'
            ],
            borderColor: [
              'rgba(196, 201, 204,1)',
              'rgba(196, 201, 204,1)',
              'rgba(196, 201, 204,1)',
              'rgba(196, 201, 204,1)'
            ],
            borderWidth: 1
          },
          {
            label: 'ICRYM',
            data: [this.personalEnMemoriaICRYM.length, this.personalRelevanteICRYM.length, this.personalProductivoICRYM.length, this.personalNuloICRYM.length],
            backgroundColor: [
              'rgba(85, 58, 153,0.9)',
              'rgba(85, 58, 153,0.7)',
              'rgba(85, 58, 153,0.5)',
              'rgba(85, 58, 153,0.3)'
            ],
            borderColor: [
              'rgba(85, 58, 153,1)',
              'rgba(85, 58, 153,1)',
              'rgba(85, 58, 153,1)',
              'rgba(85, 58, 153,1)'
            ],
            borderWidth: 1
          }]
        },
        options: {
          scales: {
            yAxes: [{
              stacked: true
            }]
          },
          onClick: function (ev, legend) {
            if (legend.length > 0) {
              let vueThis = window.$vue.$children[0].$children[1]
              let label = legend[0]._model.label.split(' ')[1].slice(0, -1)
              let labels = legend.map(x => x._model.datasetLabel)
              let labeMap = labels.map(label => {
                if (label === 'IDAES') return 'INST.DE ALTOS ESTUDIOS SOCIALES'
                if (label === 'EH') return 'ESCUELA DE HUMANIDADES'
                if (label === 'EEYN') return 'ESCUELA DE ECONOMIA Y NEGOCIOS'
                if (label === 'ECYT') return 'ESCUELA DE CIENCIA Y TECNOLOGIA'
                if (label === 'IIB') return 'INST.DE INVEST.BIOTECNOLOGICAS'
                if (label === 'IIPC') return 'TAREA-INSTITUTO DE INVESTIGACIONES SOBRE EL PATRIMONIO CULTURAL'
                if (label === '3IA') return 'INST.DE INV. E INGENIERIA AMBIENTAL'
                if (label === 'IS') return 'INSTITUTO SABATO'
                if (label === 'EPYG') return 'ESCUELA DE POLITICA Y GOBIERNO'
                if (label === 'IK') return 'INSTITUTO DE ARTES MAURICIO KAGEL'
                if (label === 'ICRYM') return 'INST.DE CS.DE LA REHABILITACION Y EL MOVIMIENTO'
              })
              if (label === 'Memoria') {
                vueThis.dialogPersonalEnMemoriaLabels = vueThis.personalEnMemoria.filter(x => labeMap.includes(x.organizacion)).sort((x, y) => x.organizacion_id - y.organizacion_id)
                vueThis.openDialog('dialogPersonalEnMemoria')
              }
              if (label === 'Relevante') {
                vueThis.dialogPersonalRelevanteLabels = vueThis.listadoPersonalRelevante.filter(x => labeMap.includes(x.organizacion)).sort((x, y) => x.organizacion_id - y.organizacion_id)
                vueThis.openDialog('dialogPersonalRelevante')
              }
              if (label === 'Productivo') {
                vueThis.dialogPersonalProductivoLabels = vueThis.listadoPersonalProductivo.filter(x => labeMap.includes(x.organizacion)).sort((x, y) => x.organizacion_id - y.organizacion_id)
                vueThis.openDialog('dialogPersonalProductivo')
              }
              if (label === 'Nulo') {
                vueThis.dialogPersonalNuloLabels = vueThis.listadoPersonalNulo.filter(x => labeMap.includes(x.organizacion)).sort((x, y) => x.organizacion_id - y.organizacion_id)
                vueThis.openDialog('dialogPersonalNulo')
              }
            }
          }
        }
      })
    },
    openDialog (ref) {
      this.$refs[ref].open()
    },
    closeDialog (ref) {
      this.$refs[ref].close()
    }
  }
}
</script>



